.footer-parent{
    width: 100%;
    height: 400px;
    background-color: rgba(0, 0, 0, 0.507);
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    justify-content: space-between;

}

.footer-container{
    display: flex;
    justify-content: space-evenly;
    height: 70%;
    align-items: center;
    padding: 0px 100px 0px 10px;
}

.listTitle-footer{
    font-size: 25px;
    font-weight: bold;
    font-family: sans-serif;
}

.footer-desc{
    padding: 20px;
}

.lists{
    margin: 0;
    margin-top: 30px !important;
    display: flex;
    width: 15%;
    height: 90%;
    justify-content: flex-start;
}

.column{
    flex-direction: column;
}

.lists ul{
    list-style: none;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.copyright{
    font-size: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 105px;
    flex-direction: column;
}

.copyright hr{
    width: 100%;
}

.footer-logo{
    height: 70px;
}

.footer-icon{
    height: 15px;
    margin-right: 10px;
    cursor: pointer;
}

.li-footer{
    cursor: pointer;
    display: flex;
    align-items: center;
}

.li-footer a{
    text-decoration: none;
    color: inherit;
}