.steps-parent{
    margin-top: 150px;
    display: flex;
    flex-direction: column;
    padding: 0 150px 0 1 50px;
}

.steps{
    font-size: 35px;
    font-weight: bold;
    font-family: sans-serif;
    display: flex;
    justify-content: center;
}

.carts-container{
    display: flex;
    margin-top: 80px;
}

.cart-parent{
    display: flex;
    justify-content: space-evenly;
    height: 280px;
    width: 100%;
}

.container{
    width: 80%;
    width: 80%;
    background-color: rgb(230 230 230);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 15px;
    transition: 500ms;
}

.cart-img{
    height: 65px;
    padding: 14px;
    background-color: white;
    border-radius: 45%;
}

.cart-parent .container:hover{
    background-color: #6c798f;
    color: white;
}

.cart-title{
    font-size: 22px;
    font-weight: bold;
    font-family: system-ui;
}

.cart-desc{
    padding: 10px;
}

.down{
    margin-top: 120px;
}

.down .container{
    background-color: #6c798f;
    color: white;
    transition: 500ms;
}

.down .container:hover{
    background-color: rgb(230 230 230);
    color: black;
}