.resume-parent{
    display: flex;
    justify-content: space-evenly;
    margin-top: 150px;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: column;
}

.boxes-parent{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.Resumes{
    display: flex;
    justify-content: center;
    font-size: 35px;
    font-weight: bold;
    font-family: sans-serif;
}

.box-resume{
    width: 35%;
    height: 330px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    flex-direction: column;
}

.box-container{
    width: 90%;
    height: 90%;
    margin: 0;
}

.img-box{
    height: 100%;
    width: 100%;
    border-radius: 15px;
    cursor: pointer;
    transition: 500ms;
}

.img-box:hover{
    transform: scale(1.03);
    box-shadow:rgb(0 0 0 / 10%) 0px 0px 7px 3px;
}

.imgdesc-box{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-family: system-ui;
    margin-top: 10px;
    cursor: pointer;
}