.header-parent{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.aboutMe{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 140px;
}

.header-dec{
    display: flex;
    flex-direction: column;
    height: 350px;
    justify-content: space-evenly;
}

.header-img{
    height: 380px;
    margin: 0;
}

h1{
    margin: 0;
}

.me{
    font-size: 15px;
    font-family: system-ui;
    margin: 0;
}

.order-btn{
    width: 45%;
    height: 40px;
    border-radius: 8px;
    background-color: transparent;
    margin-left: 100px;
    font-size: 15px;
    border: solid #595057 0.5px;
    transition: 500ms;
    cursor: pointer;
}

.order-btn:hover{
    border: none;
    background-color: rgba(87, 104, 123, 0.392);
    color: white;
}
