.nav-parent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.808);

}

.nav-ul{
    display: flex;
    margin: 0;
    padding: 0;
    margin-right: 50px;
}

.nav-list{
    list-style: none;
    height: 65px;
    width: 150px;
    padding: 10px;
    cursor: pointer;
    transition: 500ms;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
}

.nav-list:hover{
    background-color: lightgray;
}

.site-logo{
    height: 40px;
    margin-left: 80px;
    cursor: pointer;
}