.services-parent{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    flex-wrap: wrap;
    margin-top: 150px;
}

.servisec{
    font-size: 35px;
    font-family: sans-serif;
    font-weight: bold;
    display: flex;
    justify-content: center;
}

.servicebox-parent{
    width: 45%;
    display: flex;
    justify-content: center;
}

.servBoxes-parent{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.serv-container{
    display: flex;
    justify-content: center;
    width: 75%;
    border-radius: 15px;
    background-color: rgba(82, 82, 88, 0.09);
    flex-direction: column;
    align-items: center;
    transition: 500ms;
}

.serv-container:hover{
    color: white;
    background-color: #6c798f50;
}

.servbox-img{
    margin-top: 15px;
    height: 50px;
}

.serv-container h5{
    margin: 25px 6px 0px 6px;
    font-size: 20px;
}

.serv-container p{
    padding: 15px;
    color: #00000099;
}

.cart-margintop{
    margin-top: 25px;
}