.me-parent{
    width: 100%;
    height: 350px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    background-image: url('/public/images/me-background.png');
    background-size: cover;
    margin-top: 100px;
    box-shadow: rgb(0 0 0 / 35%) 0px 0px 10px 1px;
}

.me-image{
    height: 100px;
    margin-top: 50px;
}

.me-description{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.me-role{
    font-weight: bold;
    font-size: 15px;
    font-family: monospace;
    color: white;
}

.me-name{
    font-weight: bold;
    font-size: 30px;
    font-family: monospace;
    color: white;
    
}